import React, { useState } from 'react'
import PlayerList from './PlayerList'

function Search({query, setQuery, players}) {
  const [search, setSearch] = useState('')
  const [filter, setFilter] = useState('name')

  const handleChange = e => {
    setSearch(e.target.value)
  }
  const filterChange = e => {
    setFilter(e.target.value)
    console.log(e.target.value)
  }

  const submitHandler = e =>{
    e.preventDefault()
    setQuery(search)
    console.log(search)
  }

  return (
    <div className="search-page">
      <div className="search-page-container">
        <div className="search-box-container">
          <h1>FIFA DATABASE</h1>
          <form onSubmit={(e)=>submitHandler(e)}>
            <div className="search-box">
              <input type="text" value={search} onChange={(e)=>handleChange(e)} autoComplete='off'/>
              <select name="searcher" id="search" value={filter} onChange={(e)=>filterChange(e)}>
                <option value="name">Name</option>
                <option value="club">Club</option>
                <option value="nation">Nation</option>
              </select>
            </div>
          </form>
          {search && <PlayerList search={search} filter={filter} players={players} />}
        </div>
      </div>
    </div>
  )
}

export default Search
