import { Link, useParams } from 'react-router-dom'
import { Radar } from 'react-chartjs-2'


function Player({ match, players }) {
  
  const { name } = useParams()
  
  const specifiedPlayer = players.filter((item) => {
    return item.Name.replace(' ', '+') === name
  })
  
  const player = specifiedPlayer[0]
  
  console.log(player)
  
  const data = {
    labels: [ 'Overall','Crossing', 'Finishing', 'Heading Accuracy', 'Short Passing', 'Volleys'],
    datasets: [
      {
        label: '# of Stats',
        data: [ player.Overall,player.Crossing, player.Finishing, player.HeadingAccuracy, player.ShortPassing, player.Volleys],
        backgroundColor: 'rgba(255, 99, 132, 0.2)',
        borderColor: 'rgba(255, 99, 132, 1)',
        borderWidth: 1,
      },
    ],
  }
  
  const options = {
    scale: {
      ticks: { beginAtZero: true },
    },
  }

  return (
    <div className="player-details">
      <div className="player-details-container">
        <div className="header">
          <div className="left">
            <Link to="/" style={{ textDecoration: 'none', color: '#b8b8b8' }}>
              <i className="fa-solid fa-arrow-left"></i>
              Home
            </Link>
            <a href="#" style={{ textDecoration: 'none', color: '#b8b8b8' }}>
              <i className="fa-solid fa-question"></i>
              Help
            </a>
          </div>
          <div className="mid">
            <h1 className="player-name">{player.Name}</h1>
          </div>
          <div className="right"></div>
        </div>
        <main className="main">
          <div className="player-graph">
            {/* Chart JS Chart */}
            {/* <canvas id="myChart" width="400" height="400"></canvas> */}
            <Radar data={data} options={options} />
          </div>
          <div className="player-stats">
            <div className="head">
              <span>Overall {player.Overall} </span>
            </div>
            <div className="sub-stats">
              <p>
                Age : <span className="stats-value">{player.Age}</span>{' '}
              </p>
              <p>
                Height : <span className="stats-value">{player.Height}</span>{' '}
              </p>
              <p>
                Weight : <span className="stats-value">{player.Weight}</span>{' '}
              </p>
              <p>
                Preferred Foot :{' '}
                <span className="stats-value">{player.PreferredFoot}</span>{' '}
              </p>
              <p>
                Position :{' '}
                <span className="stats-value">{player.Position}</span>{' '}
              </p>
              <p>
                Jersey Number :{' '}
                <span className="stats-value">{player.JerseyNumber}</span>{' '}
              </p>
              <p>
                Club : <span className="stats-value">{player.Club}</span>{' '}
              </p>
              <p>
                Value : <span className="stats-value">{player.Value}</span>{' '}
              </p>
              <p>
                Contract :{' '}
                <span className="stats-value">{player.ContractValidUntil}</span>{' '}
              </p>
              <p>
                Work Rate :{' '}
                <span className="stats-value">{player.WorkRate}</span>{' '}
              </p>
            </div>
          </div>
        </main>
      </div>
    </div>
  )
}

export default Player
